.LogoutButton {
  background-color: yellow;
  border: none;
  padding: 10px 20px;
  border-radius: 10px;
  margin-top: 10px;
  cursor: pointer;
}
.EditDeleteProfileWrapper {
  padding: 20px;
}
.EditDeleteProfileWrapper button {
  margin: 5px;
  color: white;
  padding: 10px;
  border-radius: 10px;
  border: none;
}
.EditDeleteProfileWrapper button:first-child {
  background: blue;
}
.EditDeleteProfileWrapper button:nth-child(2) {
  background: red;
}
.ActiveButton {
  background-color: green !important;
}
